import CanvasDraw from 'react-canvas-draw';
import { useState, useRef } from 'react';
import '../css/canvas.css';
import html2canvas from 'html2canvas';
import Spody from './Spody';


function save() {
    const area = document.querySelector('.save');
    html2canvas(area)
        .then(canvas => {
            const url = canvas.toDataURL();
            const download = document.createElement('a');
            download.download = `hello.png`;
            download.href = url;
            download.click();
        });
}

const Canvas = () => {

    // const fetchImg=(url)=>{
    //     fetch(url)
    //     .then(res=>res.blob())
    //     .then(r=>console.log(r))
    // }
    // const resetConfig = {
    //     Skin: "#ffffff",
    //     Tshirt: "#ffffff",
    //     Eye: "#ffffff",
    //     Border: "#121212"
    // };
    const [config, setConfig] = useState({
        Radius: 5,
        Brushcolor: '#000000',
        Bg: ''
    });

    const [spodyConfig, setSpodyConfig] = useState({
        Skin: "#FED1D0",
        Tshirt: "#2875FF",
        Eye: "#ffffff",
        Border: "#121212",
        Display: "block"
    });

    const canvasRef = useRef(null);

    return (

        <div className="container">
            
            {/* radius slider, color picker circular, ##background-img behing spood */}
            <div className="playground">
                <h2>Playground</h2>

                <div className="brush-view">
                    <div className="brush-preview" style={{ height: `${config['Radius'] * 2}px`, width: `${config['Radius'] * 2}px`, backgroundColor: config['Brushcolor'] }}>
                    </div>

                </div>

                <div className="Brush">
                    <span>Radius</span>
                    <input type="range" min="1" max="50" defaultValue={config['Radius']} onChange={(e) =>
                        setConfig({ ...config, Radius: parseInt(e.target.value) })} />
                    <div className="colorpicker">
                        <span>Brush Color</span>
                        <input type="color" defaultValue={config['Brushcolor']} onChange={(e) => setConfig({ ...config, Brushcolor: e.target.value })} />
                    </div>

                    <div className="spody">
                        <span className="spody-title">Spody</span>
                        <div className="colorpicker">
                            <span>Skin</span>
                            <input type="color"
                                defaultValue={spodyConfig['Skin']}
                                onChange={(e) => setSpodyConfig({ ...spodyConfig, Skin: e.target.value })} />
                        </div>

                        <div className="colorpicker">
                            <span>Tshirt</span>
                            <input type="color"
                                defaultValue={spodyConfig['Tshirt']}
                                onChange={(e) => setSpodyConfig({ ...spodyConfig, Tshirt: e.target.value })} />
                        </div>

                        <div className="colorpicker">
                            <span>Eye</span>
                            <input type="color"
                                defaultValue={spodyConfig['Eye']}
                                onChange={(e) => setSpodyConfig({ ...spodyConfig, Eye: e.target.value })} />
                        </div>

                        <div className="colorpicker">
                            <span>Border-Line</span>
                            <input type="color"
                                defaultValue={spodyConfig['Border']}
                                onChange={(e) => setSpodyConfig({ ...spodyConfig, Border: e.target.value })} />
                        </div>
                        {/* <div className="bg">
                            <span>Background</span>
                            <input type="text" placeholder="Paste Url"
                            onChange={(e)=> fetchImg(e.target.value)} />
                        </div> */}
                        <div className="colorpicker ">
                            {spodyConfig.Display === "block" && <button className="hide-show"
                                onClick={() => {
                                    setSpodyConfig({ ...spodyConfig, Display: "none" });
                                }
                                } >Hide</button>}

                            {spodyConfig.Display === "none" && <button className="hide-show"
                                onClick={() => {
                                    setSpodyConfig({ ...spodyConfig, Display: "block" });
                                }
                                } >Show</button>}

                            <div className="github">
                                <a href="https://github.com/kvnal">@github.com/kvnal</a>
                            </div>


                        </div>


                    </div>
                </div>
            </div>


            <div className="canvas-container">


                <div className="canvas-control">
                    <button className="hide-show" onClick={() => canvasRef.current.undo()} >↩ Undo</button>
                    <button className="hide-show" onClick={() => canvasRef.current.clear()} >⊗ Clear</button>
                    <button className="hide-show" onClick={() => {
                        window.location.reload();
                        // canvasRef.current.clear()
                        // setSpodyConfig({...resetConfig})}
                    }}>↻ Reset</button>
                </div>

                <div className="save">

                    <div className="canvas">
                        <CanvasDraw ref={canvasRef}
                            canvasHeight={540}
                            canvasWidth={540}
                            brushColor={config['Brushcolor']} lazyRadius={0}
                            brushRadius={config['Radius']} hideGrid={true}
                            catenaryColor={''}
                        // imgSrc={}
                        />

                    </div>
                    <div className="svg">
                        <Spody Skin={spodyConfig['Skin']} Tshirt={spodyConfig['Tshirt']} Eye={spodyConfig['Eye']}
                            Border={spodyConfig['Border']}
                            Display={spodyConfig['Display']}
                        />
                    </div>
                </div>

                {/* <div className="save-button">
                    <button onClick={() => save()}> save</button>
                </div> */}
            </div>
        </div>

    );
};

export default Canvas;